<template>
    <div class="container-fluid">
        <TopButton title="카테고리별" />
        <!-- 탭 메뉴 영역 -->
        <div class="menu-swiper">
            <!-- Swiper 슬라이드 -->
            <swiper :slides-per-view="3" :space-between="30" grabCursor="true">
                <swiper-slide
                    v-for="(item, index) in menuItems"
                    :key="index"
                    :class="{ active: activeTab === index }"
                    @click="setActiveTab(index)"
                >
                    {{ item.name }}
                </swiper-slide>
            </swiper>
        </div>
        <div class="container text-center">
            <div class="row">
                <div class="col-auto me-auto">
                    <div class="form-check">
                        <input class="form-check-input" type="checkbox" value="" id="flexCheckChecked" checked />
                        <label class="form-check-label" for="flexCheckChecked"> 내 피부타입만 보기 </label>
                    </div>
                </div>
                <div class="col-auto">
                    <div class="dropdown">
                        <button
                            class="btn btn-secondary dropdown-toggle"
                            type="button"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                        >
                            추천순
                        </button>
                        <ul class="dropdown-menu">
                            <li><button class="dropdown-item" type="button">인기순</button></li>
                            <li><button class="dropdown-item" type="button">신상품순</button></li>
                            <li><button class="dropdown-item" type="button">저가순</button></li>
                            <li><button class="dropdown-item" type="button">고가순</button></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <div class="tab-content">
            <!-- 현재 activeTab에 해당하는 콘텐츠만 렌더링 -->
            <div v-if="menuItems[activeTab]">
                {{ menuItems[activeTab].content }}
            </div>
            <!-- 상품  -->
            <div class="contents" v-if="activeTab === 0">
                <div class="itemBox" v-for="(items, i) in item" :key="i" @click="goToDetail(items.id)">
                    <div class="itemImg">
                        <img :src="items.image" alt="상품이미지" />
                    </div>
                    <div class="itemText">
                        <p>#000#000#000</p>
                        <p>4.3점</p>
                        <p>{{ items.title }}</p>
                        <p>{{ items.price }} 원</p>
                    </div>
                </div>
            </div>
            <div class="contents" v-if="activeTab === 1">
                <div class="itemBox" v-for="(items2, i) in item2" :key="i" @click="goToDetail(items2.id)">
                    <div class="itemImg">
                        <img :src="items2.image" alt="상품이미지" />
                    </div>
                    <div class="itemText">
                        <p>#000#000#000</p>
                        <p>4.3점</p>
                        <p>{{ items2.title }}</p>
                        <p>{{ items2.price }} 원</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import TopButton from '../components/TopButton.vue';
import CategoryItem from '../assets/js/CategoryItem.js';
import CategoryItem2 from '../assets/js/CategoryItem2.js';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/swiper-bundle.css';

export default {
    name: 'Category',
    components: {
        TopButton,
        Swiper,
        SwiperSlide
    },
    data() {
        return {
            item: CategoryItem,
            item2: CategoryItem2,
            activeTab: 0,
            menuItems: [
                { name: '비누/클렌징', content: '' },
                { name: '마스크/팩/필링', content: '마스크, 팩, 필링 관련 내용입니다.' },
                { name: '스킨/토너/미스트', content: '스킨, 토너, 미스트 관련 내용입니다.' },
                { name: '앰플/에센스/세럼', content: '앰플, 에센스, 세럼 관련 내용입니다.' },
                { name: '수분젤/에센셜', content: '선크림 관련 내용입니다.' },
                { name: '로션/크림', content: '크림, 로션 관련 내용입니다.' },
                { name: '썬크림', content: '미용 기기 관련 내용입니다.' }
            ]
        };
    },
    methods: {
        setActiveTab(index) {
            this.activeTab = index;
        },
        goToDetail(items) {
            this.$router.push({
                name: 'ProductPage',
                params: {
                    id: items,
                    image: items.image,
                    title: items.title,
                    price: items.price
                }
            });
        }
    }
};
</script>
<style scoped>
/* 탭 내용 */
.menu-swiper {
    margin: 15px 0;
}
.swiper-slide.active {
    background: #d1d1d1;
    border-radius: 20px;
}
.tab-content {
    margin-top: 20px;
    padding: 10px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 10px;
}

/* 상품  */
.contents {
    margin: 0 auto;
    max-width: 390px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.itemBox {
    margin-top: 10px;
    width: 170px;
    height: 260px;
}
.itemBox .itemImg {
    margin: 0 auto;
    width: 170px;
    height: 170px;
    background-color: #d1d1d1;
}
.itemBox .itemImg img {
    display: block;
    width: 170px;
    height: 170px;
}
.itemBox .itemText p {
    text-align: left;
    margin: 0;
    font-size: 0.85em;
}
</style>
